.sidebar {
  width: 200px;
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 20px 0;
}

.sidebar-top {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar-bottom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar-item {
  background: none;
  border: none;
  text-align: left;
  padding: 12px 16px;
  font-size: 16px;
  color: #333;
  cursor: pointer;
  width: 100%;
  border-radius: 5px;
  transition: background 0.2s;
}

.sidebar-item:hover {
  background-color: #e0e0e0;
}

.chat-btn {
  color: #007bff;
}

.logout-btn {
  color: red;
}
