/* Home.css */

.home-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f8f9fa;
  font-family: Arial, sans-serif;
}

.home-body {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.home-content {
  flex: 1;
  background-color: #ffffff;
  padding: 16px;
  overflow-y: auto;
}

.scroll-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 24px;
  color: #007bff;
}
