.product-details-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 16px;
  background: #fff;
  border-radius: 8px;
}

.back-button {
  background: none;
  border: none;
  color: #007bff;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 12px;
}

.image-wrapper {
  position: relative;
  text-align: center;
  margin-bottom: 16px;
}

.product-image {
  width: 100%;
  max-height: 250px;
  object-fit: contain;
  border-radius: 12px;
}

.change-image-label {
  display: inline-block;
  margin-top: 8px;
  color: #007bff;
  cursor: pointer;
  font-size: 14px;
}

.change-image-label input {
  display: none;
}

.product-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
  box-sizing: border-box;
}

textarea.product-input {
  height: 80px;
  resize: vertical;
}

.update-button {
  width: 100%;
  padding: 14px;
  background-color: #28a745;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.update-button:hover {
  background-color: #218838;
}
