.user-profile-container {
  padding: 24px;
  background-color: #fff;
  width: 400px;
  max-width: 90%;
  margin: 40px auto;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  text-align: center;
}

.info-row {
  display: flex;
  margin-bottom: 16px;
}

.label {
  font-weight: 600;
  width: 80px;
}

.value {
  flex: 1;
  font-weight: 400;
}

.close-btn {
  margin-top: 32px;
  background-color: #333;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  padding: 12px;
  width: 100%;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.close-btn:hover {
  background-color: #555;
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}
