.banner-container {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  margin-bottom: 16px;
}

.banner-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  top: 0;
  left: 100%; /* start off-screen */
  opacity: 0;
  transition: all 0.5s ease-in-out;
  display: flex;
  align-items: flex-end;
  padding: 0 20px 20px 20px;
}

.banner-slide.active {
  left: 0;
  opacity: 1;
}

.banner-button {
  background-color: rgba(0,0,0,0.6);
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.banner-button:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .banner-container {
    height: 150px;
  }

  .banner-button {
    font-size: 12px;
    padding: 6px 10px;
  }
}