.quotation-panel {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.quotation-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #fff;
}

.file-input {
  margin-top: 10px;
}

.close-btn {
  margin-top: 20px;
  padding: 10px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.close-btn:hover {
  background-color: #0056b3;
}
