.product-details {
  max-width: 600px;
  margin: 0 auto;
  padding: 16px;
  background: #fff;
  border-radius: 12px;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}

.back-btn {
  background: none;
  border: none;
  color: #007bff;
  font-size: 16px;
  cursor: pointer;
}

.quote-btn {
  background: #28a745;
  color: #fff;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 700;
}

.product-image {
  width: 100%;
  height: 300px;
  object-fit: contain;
  border-radius: 12px;
  margin-bottom: 16px;
}

.product-name {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 4px;
}

.product-category {
  color: #28a745;
  margin-bottom: 8px;
}

.product-price {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 16px;
  color: #28a745;
}

.product-description {
  font-size: 14px;
  color: #555;
  margin-bottom: 20px;
}

.quantity-row {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.qty-btn {
  background: #333;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.qty-input {
  width: 50px;
  text-align: center;
  margin: 0 12px;
  font-size: 18px;
  padding: 4px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.add-to-cart-btn {
  background: #333;
  color: #fff;
  padding: 14px;
  font-weight: 700;
  font-size: 18px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  width: 100%;
}
