.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #f8f9fa;
}

.body {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.content {
  flex: 1;
  background: #fff;
  padding: 16px;
  overflow-y: auto;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #007bff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 12px 0;
}

.section-heading {
  font-size: 18px;
  font-weight: 700;
  color: #333;
}

.add-button {
  font-size: 16px;
  font-weight: 700;
  color: #007bff;
  background: none;
  border: none;
  cursor: pointer;
}

.add-button:hover {
  text-decoration: underline;
}

.scrollable {
  padding-bottom: 40px;
}

.notifications-container {
  padding: 16px;
}

.notifications-container h2 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 12px;
}

.notification {
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 6px;
  border: 1px solid #ddd;
}

.notification.unread {
  background-color: #f5f5f5;
}

.notification.read {
  background-color: #fff;
}

.timestamp {
  display: block;
  font-size: 10px;
  color: #888;
  margin-top: 4px;
}
