.seller-chat {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background: #fdfdfd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.close-btn {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 8px 14px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 16px;
}

.close-btn:hover {
  background-color: #c82333;
}

.conversation-list {
  list-style: none;
  padding: 0;
}

.conversation-list li {
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}

.conversation-list li:hover {
  background-color: #f1f1f1;
}

.messages-list {
  max-height: 300px;
  overflow-y: auto;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background: #fff;
}

.message-item {
  margin-bottom: 8px;
  padding: 4px 8px;
  border-radius: 5px;
}

.message-item.own {
  background-color: #d1ffd6;
  text-align: right;
}

.message-item.other {
  background-color: #f1f1f1;
  text-align: left;
}

.message-input-row {
  display: flex;
  gap: 8px;
}

.message-input {
  flex: 1;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.send-btn {
  padding: 8px 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.send-btn:hover {
  background-color: #0056b3;
}
