.add-container {
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  max-width: 500px;
  margin: auto;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.add-heading {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.add-input {
  width: 100%;
  border: 1px solid #333;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 12px;
  font-size: 14px;
}

.textarea {
  height: 80px;
  resize: vertical;
}

.image-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.image-label {
  margin-right: 10px;
  font-size: 16px;
  font-weight: 500;
}

.file-name {
  font-size: 12px;
  color: gray;
  margin-bottom: 12px;
  margin-top: -8px;
}

.submit-btn {
  width: 100%;
  background-color: #333;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #555;
}

.cancel-btn {
  margin-top: 16px;
  width: 100%;
  background: none;
  border: none;
  color: red;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
}
