.sidebar {
  width: 220px;
  height: 100vh;
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 0;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.menu-items {
  display: flex;
  flex-direction: column;
}

.sidebar-item {
  padding: 12px 20px;
  font-size: 16px;
  color: #333;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  transition: background 0.2s;
}

.sidebar-item:hover {
  background-color: #e0e0e0;
}

.sidebar-item.logout {
  color: red;
  font-weight: bold;
}
